<template>
  <main class="dashboard">
    <StateBox :patientStatus="patientStatus" />
    <section class="dashboard__chart__wrap">
      <div class="donut__chart__wrap">
        <div class="donut__list">
          <div class="box__shadow__01">
            <h3>연령대별 환자수</h3>
            <AgeChart ref="ageChart"></AgeChart>
          </div>
          <div class="box__shadow__01">
            <h3>몸무게별 환자수</h3>
            <WeightChart ref="weightChart"></WeightChart>
          </div>
        </div>
        <div class="area__chart__box box__shadow__01">
          <h3>앱 사용시간</h3>
          <UsageTimeChart
            ref="usageTimeChart"
            @goPatientDashboard="goPatientDashboard"
          ></UsageTimeChart>
        </div>
      </div>
      <div class="bar__chart__wrap">
        <div class="box__shadow__01">
          <h3>회복지수</h3>
          <RecoveryChart
            ref="recoveryChart"
            @goPatientDashboard="goPatientDashboard"
          ></RecoveryChart>
        </div>
        <div class="box__shadow__01">
          <h3>단주유지일</h3>
          <StopDrinkChart
            ref="stopDrinkChart"
            @goPatientDashboard="goPatientDashboard"
          ></StopDrinkChart>
        </div>
      </div>
    </section>
    <PatientGrid
      ref="paitentListGrid"
      @goPatientDashboard="goPatientDashboard"
    ></PatientGrid>
  </main>
</template>
<script>
import "@/assets/css/dashboard.css";
import StateBox from "@/components/Dashboard/StateBox";
import AgeChart from "@/components/Dashboard/DoughnutChart01";
import WeightChart from "@/components/Dashboard/DoughnutChart02";
import UsageTimeChart from "@/components/Dashboard/UsageTimeChart";
import RecoveryChart from "@/components/Dashboard/RecoveryChart";
import StopDrinkChart from "@/components/Dashboard/StopDrinkChart";
import PatientGrid from "@/components/Dashboard/PatientGrid";
import data5 from "@/data/dashboard/astop/data5.json";
// import { getAstopPatient } from "@/api/dashboard";
// import { getPatientDashboard } from "@/api/patient";

export default {
  components: {
    StateBox,
    AgeChart,
    WeightChart,
    PatientGrid,
    RecoveryChart,
    StopDrinkChart,
    UsageTimeChart,
  },
  created() {},
  mounted() {
    this.$refs.ageChart.ageChartInit();
    this.$refs.weightChart.weightChartInit();
    this.$refs.usageTimeChart.chartInit();
    this.$refs.recoveryChart.chartInit();
    this.$refs.stopDrinkChart.chartInit();
    this.$refs.paitentListGrid.gridInit();
  },
  methods: {
    goPatientDashboard(params) {
      this.$confirm({
        // showCancelButton: true,
        title: "",
        message: `'${params.name}' 환자 대시보드 화면으로 이동하시겠습니까?`,
        button: {
          no: "아니오",
          yes: "네",
        },
        callback: (confirm) => {
          if (confirm) {
            const resDashboard = data5;
            resDashboard.age =
              Number.parseInt(new Date().getFullYear()) -
              Number.parseInt(resDashboard.birthDay.slice(0, 4)) +
              1;
            if (resDashboard.sex === "M") {
              resDashboard.sex = "남성";
            } else {
              resDashboard.sex = "여성";
            }
            this.$router.push({
              name: "PatientDashboard",
              params: resDashboard,
            });
          }
        },
      });
    },
  },
  data() {
    return {
      patientStatus: {},
      ageList: {
        label: [],
        value: [],
      },
      weightList: {
        label: [],
        value: [],
      },
    };
  },
};
</script>
