<template>
  <div>
    <h1>AdminPage</h1>
    <br />
    <div>
      <label for="fileInput">
        <img src="@/assets/images/icon/ic-plus.svg" alt="미디어 추가 아이콘" />
        <p>update sample file</p>
      </label>
      <input
        class="file"
        @change="uploadFile($event.target)"
        type="file"
        ref="csvInput"
        id="csvInput"
      />
      <LoadingBar v-if="isLoading" />
    </div>
    <div class="grid__wrap" @click="rowClick($event)">
      <Grid
        class="on"
        ref="gridEl"
        :cols="cols"
        :pagination="true"
        :sort="true"
        :rows="fileList"
      ></Grid>
    </div>
  </div>
</template>

<script>
import "@/assets/css/commonCodeManagement.css";
import { Grid } from "gridjs-vue";
import LoadingBar from "@/components/common/LoadingBar";
import { getFilelist, upload } from "@/utill/getData";

export default {
  name: "AdminPage",
  components: {
    Grid,
    LoadingBar,
  },
  data() {
    return {
      fileList: [],
      isLoading: false,
      cols: [
        { name: "파일 명", id: "Key" },
        { name: "크기", id: "Size" },
        { name: "타입", id: "Type" },
        { name: "등록일", id: "LastModified" },
      ],
    };
  },
  mounted() {
    getFilelist().then((list) => {
      list.forEach((obj) => {
        //obj.LastModified = Date.parse(obj.LastModified);
        if (obj.Size === 0) {
          obj.Type = "Folder";
        } else {
          obj.Type = "File";
        }
        let year = obj.LastModified.getFullYear();
        let month = obj.LastModified.getMonth() + 1;
        let day = obj.LastModified.getDate();
        let date = year + "-" + month + "-" + day;
        obj.LastModified = date;
      });
      this.fileList = list;
    });
  },
  methods: {
    uploadFile(param) {
      let vm = this;
      if (param.files.length > 0) {
        let uploadFile = param.files[0];
        vm.isLoading = true;
        let params = {
          Key: uploadFile.name,
          ContentType: uploadFile.type,
          Body: uploadFile,
        };
        upload(
          params,
          function (err, data) {
            if (err) {
              if (this.$env == "development")
                console.log("upload error", err.code, err.message);
            } else {
              // finsh uploading and close popup
              if (this.$env == "development") console.log("upload success");
              let url = data.Location; // link
              if (this.$env == "development") console.log(url);
            }
            vm.isLoading = false;
          },
          function (e) {
            let precent = Math.trunc(
              (parseInt(e.loaded, 10) / parseInt(e.total, 10)) * 100
            );
            if (this.$env == "development")
              console.log(`upload video file ${uploadFile.name} - ${precent}%`);
          }
        );
      }
    },
    rowClick(param) {
      if (this.$env == "development") console.log(param);
    },
  },
};
</script>
