<template>
  <section class="grid__wrap box__shadow__01">
    <div class="grid__title">
      <h3>진료중인 환자 정보</h3>
      <button class="custom__btn" @click="fn_excelExport">
        <span>
          <img src="@/assets/images/dashboard/ic-excel.svg" alt="엑셀 아이콘" />
        </span>
        <p>엑셀저장</p>
      </button>
    </div>
    <div class="dashboard__astop__grid" @click="rowClick($event)">
      <Grid
        id="gridEl"
        :cols="cols"
        :pagination="true"
        :sort="true"
        :autoWidth="true"
        :rows="paitentList"
      ></Grid>
    </div>
  </section>
</template>
<script>
import Grid from "gridjs-vue";
import { jsontoexcel } from "vue-table-to-excel";
import data4 from "@/data/dashboard/astop/data4.json";
export default {
  components: {
    Grid,
  },
  data() {
    return {
      cols: [
        { name: "병록번호", id: "patientRegNumber" },
        { name: "이름", id: "patientName" },
        { name: "성별", id: "sex" },
        { name: "생년월일", id: "birthday" },
        { name: "치료시작일", id: "startDate" },
        { name: "치료경과일수", id: "elapsedDay" },
        { name: "출석율(%)", id: "attendanceRate" },
        { name: "총단주일", id: "totalAStopDay" },
        { name: "연속단주일", id: "continuousAStopDay" },
        { name: "최장단주일", id: "longestAStopDay" },
        { name: "약복용률(%)", id: "medicationRate" },
      ],
      exportExcelGridHead: [
        "병록번호",
        "이름",
        "성별",
        "생년월일",
        "치료시작일",
        "치료경과일수",
        "출석율(%)",
        "총단주일",
        "연속단주일",
        "최장단주일",
        "약복용률(%)",
      ],
      today: new Date(),
      paitentList: [],
    };
  },

  methods: {
    rowClick(params) {
      const cellField = params.srcElement.dataset.columnId;
      if (cellField !== null && cellField !== undefined) {
        const obj = params.srcElement.parentNode;
        const param = {
          name: obj.childNodes[1].textContent,
          patientRegNumber: obj.childNodes[0].textContent,
        };
        this.$emit("goPatientDashboard", param);
      }
    },
    gridInit() {
      let newArr = data4;
      newArr.forEach((patient, idx) => {
        if (patient.sex === "M") {
          newArr[idx].sex = "남자";
        } else if (patient.sex === "F") {
          newArr[idx].sex = "여자";
        }
      });
      this.paitentList = newArr;
    },
    fn_excelExport() {
      const excelList = [];

      for (let i of this.paitentList) {
        const obj = {
          병록번호: i.patientRegNumber,
          이름: i.patientName,
          성별: i.sex,
          생년월일: i.birthday,
          치료시작일: i.startDate,
          치료경과일수: i.elapsedDay,
          "출석율(%)": i.attendanceRate,
          총단주일: i.totalAStopDay,
          연속단주일: i.continuousAStopDay,
          최장단주일: i.longestAStopDay,
          "약복용률(%)": i.medicationRate,
        };
        excelList.push(obj);
      }
      let month =
        this.today.getMonth() < 10
          ? "0" + String(this.today.getMonth() + 1)
          : String(this.today.getMonth() + 1);
      let date =
        this.today.getDate() < 10
          ? "0" + String(this.today.getDate())
          : String(this.today.getDate());
      jsontoexcel.getXlsx(
        excelList,
        this.exportExcelGridHead,
        `${month}-${date} 진료환자 리스트.xls`
      );
    },
  },
};
</script>
