<script>
import { Scatter } from "vue-chartjs";
import data2 from "@/data/dashboard/astop/data2.json";
export default {
  extends: Scatter,
  data() {
    return {
      gradient: null,
      gradient2: null,
      resData: [{ backgroundColor: "rgba(255, 51, 255, 0.5)", data: [] }],
    };
  },
  methods: {
    chartInit() {
      for (let i of data2) {
        this.resData[0].data.push(i);
      }
      this.init();
    },
    goPatientDashboard(param) {
      this.$emit("goPatientDashboard", param);
    },
    init() {
      const goPatientDashboard = (param) => {
        this.goPatientDashboard(param);
      };
      this.renderChart(
        {
          datasets: this.resData,
        },
        {
          onClick: function (point, event) {
            const dataIdx = event[0]._index;
            const dataSetIdx = event[0]._datasetIndex;
            const param = {
              name: this.tooltip._data.datasets[dataSetIdx].data[dataIdx]
                .patientName,
              patientRegNumber:
                this.tooltip._data.datasets[dataSetIdx].data[dataIdx]
                  .patientRegNumber,
            };
            goPatientDashboard(param);
          },
          plugins: {
            labels: false,
            autocolors: {
              mode: "data",
            },
          },
          responsive: true,
          maintainAspectRatio: false,
          legend: {
            display: false,
          },
          scales: {
            yAxes: [
              {
                scaleLabel: {
                  display: true,
                  labelString: "회복지수",
                  fontStyle: "bold",
                },
                ticks: {
                  callback: function (value) {
                    return value;
                  },
                  autoSkip: true,
                  stepSize: 1,
                  suggestedMax: 10,
                  suggestedMin: -10,
                  maxRotation: 0,
                  minRotation: 0,
                },
              },
            ],
            xAxes: [
              {
                gridLines: {
                  display: false,
                },
                scaleLabel: {
                  display: true,
                  labelString: "1~84일",
                  fontStyle: "bold",
                },
                ticks: {
                  callback: function (value) {
                    return value;
                  },
                  autoSkip: true,
                  suggestedMax: 84,
                  suggestedMin: 0,
                  stepSize: 4,
                  maxRotation: 0,
                  minRotation: 0,
                },
              },
            ],
          },
          tooltips: {
            callbacks: {
              label: function (tooltipItem, data) {
                const idx = tooltipItem.index;
                const dataSetIdx = tooltipItem.datasetIndex;
                const tooltipText = [];
                const date = `경과일 : ${data.datasets[dataSetIdx].data[idx].x}일`;
                const value = `회복지수 : ${data.datasets[dataSetIdx].data[idx].y}`;
                const patientName = `환자이름 : ${data.datasets[dataSetIdx].data[idx].patientName}`;
                const patientRegNumber = `병록번호 : ${data.datasets[dataSetIdx].data[idx].patientRegNumber}`;
                tooltipText.push(date);
                tooltipText.push(value);
                tooltipText.push(patientName);
                tooltipText.push(patientRegNumber);
                return tooltipText;
              },
            },
          },
        }
      );
    },
  },
};
</script>
